@import url(https://fonts.googleapis.com/css?family=Roboto:300,300i,400&display=swap);
* {
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar;
}
html,
body,
#root {
  height: 100%;
  margin: 0;
}
.views-block, #root {
  align-items: stretch;
  background-color: transparent;
  border-radius: 0;
  border: 0;
  box-sizing: border-box;
  color: inherit;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  font-family: inherit;
  font-size: inherit;
  -webkit-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto;
  line-height: inherit;
  margin: 0;
  outline: 0;
  overflow-wrap: break-word;
  padding: 0;
  position: relative;
  text-align: left;
  text-decoration: none;
  white-space: normal;
  word-wrap: break-word;
}
.views-text {
  box-sizing: border-box;
  -webkit-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto;
  outline: 0;
  overflow-wrap: break-word;
  word-wrap: break-word;
}
.views-capture {
  background-color: transparent;
  box-sizing: border-box;
  border-radius: 0;
  border: 0;
  -webkit-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto;
  outline: 0;
  overflow-wrap: break-word;
  word-wrap: break-word;
}
.views-capture::-moz-focus-inner {
  border: 0;
  margin: 0;
  padding: 0;
}
/* remove number arrows */
.views-capture[type='number']::-webkit-outer-spin-button,
.views-capture[type='number']::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
* {
	font-family: 'Roboto', sans-serif;
}
body, html {
	background-color: ghostwhite;
}
.giphy-embed {
	width: 350px;
	max-width: 350px;
	word-break: break-all;
	padding: 5px 0;
	background-color: #fff;
	box-shadow: 0 0 2px rgba(0,0,0,.2);
	height: auto;
	border-radius: 2px;
}
.giphy-embed:hover {
	box-shadow: 0 0 8px rgba(0,0,0,.35);
}
.giphy-embed:not(:last-child) {
	margin-bottom: 40px;
}
.giphy-embed > div {
	padding: 0 10px 0 10px;
	text-align: center;
	margin-bottom: 10px;
	font-size: 1.2em;
	color: #666;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}
@-webkit-keyframes flickerAnimation {
	0%   { opacity:1; }
	50%  { opacity:0; }
	100% { opacity:1; }
}
@keyframes flickerAnimation {
	0%   { opacity:1; }
	50%  { opacity:0; }
	100% { opacity:1; }
}
.animate-flicker {
	-webkit-animation: flickerAnimation 2s 2;
	        animation: flickerAnimation 2s 2;
}
.label {
	padding: 10px;
	font-style: italic;
	font-size: 1.5em;
	margin: 40px 0;
	border-radius: 10px;
	background-color: #32e0c9;
	color: #fff;
}
